<style lang="less" scoped>
//重置密码
@import "../css/common.less";
.resetPwd {
  .back {
    color: @appColor;
    img {
      width: unit(88/@rem, rem);
      height: unit(88/@rem, rem);
    }
  }
  //验证码登录
  .login-code {
    padding: 0 unit(58/@rem, rem);
    position: relative;
    .form-item {
      border-bottom: 1px solid @borderColor;
      .phone-code {
        flex: 0 0 auto;
      }
      .change-show {
        flex: 0 0 auto;
        padding: 0 unit(10/@rem, rem);
        height: unit(40/@rem, rem);
        width: unit(40/@rem, rem);
      }
      .img {
        height: unit(40/@rem, rem);
        width: unit(40/@rem, rem);
      }
      .forget-pwd {
        color: @txt-lv3;
        position: absolute;
        right: unit(58/@rem, rem);
        bottom: unit(-70/@rem, rem);
      }
    }
  }
  //登录按钮
  .submit {
    margin: 0 unit(58/@rem, rem);
    margin-top: unit(105/@rem, rem);
    margin-bottom: unit(30/@rem, rem);
    text-align: center;
    height: unit(98/@rem, rem);
    line-height: unit(98/@rem, rem);
  }
}
</style>
<template>
  <div class="resetPwd">
    <anyiHeader title="重置密码">
      <div @click="$back" class="back fz-48" slot="left">
        <img src="../assets/back.png">
      </div>
    </anyiHeader>
    <!-- 验证码登录 -->
    <div class="login-code">
      <div class="form-item">
        <anyiInput :maxLength="16" type="text" placeholder="请输入5-16位新密码" v-model="formData.password" />
      </div>
    </div>
    <!-- 登录按钮 -->
    <div @click="submit" class="anyi-btn submit fz-36" :class="{'anyi-btn-disabled': btnDisabled}">
      {{txtConfig.formType}}
    </div>

  </div>
</template>
<script>
import { Toast } from "mint-ui";
import anyiHeader from "../components/common/header";
import anyiInput from "../components/common/anyiInput";
export default {
  components: {
    anyiInput,
    anyiHeader
  },
  data() {
    return {
      //表单参数对象
      formData: {
        account: "",
        code: "",
        password: ""
      },
      //文案对象
      txtConfig: {
        formType: "提交"
      }
    };
  },
  created() {
    //获取地址栏中的参数
    this.formData.account = this.$route.query.account;
    this.formData.code = this.$route.query.code;
  },
  computed: {
    //按钮是否禁用
    btnDisabled() {
      if (this.formData.password < 5) {
        return true;
      }
      return false;
    }
  },
  mounted() {},
  methods: {
    //忘记密码跳转
    submit() {
      //提交密码
      if (this.btnDisabled) return;
      this.$http
        .resetPwd({
          data: this.formData
        })
        .then(res => {
          //重置成功 跳转至密码登录
          Toast({
            message: "重置成功",
            iconClass: "icon anyi-icon-success",
            position: "center",
            duration: 2000
          });
          setTimeout(() => {
            this.$link(`/login?type=pwd`);
          }, 2000);
        });
    }
  }
};
</script>




